[tooltip] {
  position: relative;
}

[tooltip]:before, [tooltip]:after {
  text-transform: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  pointer-events: none;
  opacity: 0;
  font-size: .9em;
  line-height: 1;
  display: none;
  position: absolute;
}

[tooltip]:before {
  content: "";
  z-index: 1001;
  border: 5px solid rgba(0, 0, 0, 0);
}

[tooltip]:after {
  content: attr(tooltip);
  text-align: center;
  width: 110px;
  height: 47px;
  text-align: center;
  letter-spacing: -.01em;
  text-overflow: ellipsis;
  color: #222b40;
  z-index: 1000;
  background: #fff;
  border-radius: 8px;
  padding: 4px 8px;
  font-size: 10px;
  line-height: 130%;
  overflow: hidden;
  box-shadow: 0 0 10px rgba(0, 0, 0, .06), 0 10px 20px -8px rgba(0, 0, 0, .16);
}

[tooltip]:hover:before, [tooltip]:hover:after {
  display: block;
}

[tooltip=""]:before, [tooltip=""]:after {
  display: none !important;
}

[tooltip][flow^="down"]:before {
  border-top-width: 0;
  border-bottom-color: #333;
  top: 100%;
}

[tooltip][flow^="down"]:after {
  top: calc(100% + 5px);
}

[tooltip][flow^="down"]:before, [tooltip][flow^="down"]:after {
  left: 50%;
  transform: translate(-50%, .5em);
}

@keyframes tooltips-vert {
  to {
    opacity: 1;
    transform: translate(0, 100%);
  }
}

[tooltip]:not([flow]):hover:before, [tooltip]:not([flow]):hover:after, [tooltip][flow^="up"]:hover:before, [tooltip][flow^="up"]:hover:after, [tooltip][flow^="down"]:hover:before, [tooltip][flow^="down"]:hover:after {
  animation: tooltips-vert .3s ease-out forwards;
}

